video {
    width: 100vw;
}

.toplevel {
    position: relative;
}

@media screen and (max-width: 600px) {
    h1 {
        position: absolute;
        top: 25%;
        left: 5%;
        color: white;
        font-size: 25px!important;
        z-index: 3;
    }
}

@media screen and (min-width: 601px) {
    h1 {
        position: absolute;
        top: 40%;
        left: 5%;
        font-size: 40px;
        color: white;
        z-index: 3;
    }
}

.toplevel video {
    min-width: 100%;
    min-height: 100%;
    position: relative;
    z-index: 1;
}
.toplevel .overlay {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 2;
   /* background-image: 
        radial-gradient(
            circle at 20% 30%, #000000, 
            rgba(11, 39, 65, 0.32) 87%, 
            rgba(0, 0, 0, 0.0)
        ); */
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(0,123,255,.25) 0%, rgba(183,181,189,0.7542367288712359) 99%, rgba(0,212,255,1) 100%);
}

